import Vue from "vue";
import VueRouter from "vue-router";
import Home from "./views/Home";
import OnetoOne from "./views/OnetoOne";
import Notice from "./views/Notice";
import FAQ from "./views/FAQ";
import Adver from "./views/Adver"
import Gready from "./views/Gready";

import Market001 from "./views/Market001";
import Market002 from "./views/Market002";
Vue.use(VueRouter);

const router = new VueRouter({
    mode: "history",
    routes: [
        {path:"/", component: Home}, // 

        {path:"/Market001", component: Market001}, //  1 
        {path:"/Market002", component: Market002}, //  2 

        {path:"/Notice", component: Notice}, // 
        {path:"/OnetoOne", component: OnetoOne}, // 1:1
        {path:"/FAQ", component: FAQ}, // 
        {path:"/Adver", component: Adver}, // 

        {path:"/Gready", component: Gready}, //
    ]
})

export default router;